.PeerMake{
    &_input{
        width: 100%;
        font-size: 30px;
        &_you{
            color: blue;
            width: 100%;
            font-size: 30px;
        }
        &_me{
            color: red;
            width: 100%;
            font-size: 30px;
        }
    }
}
.RemoteList{
    margin: 10px;
    // border: 3px solid grey;
    width: max-content;
    box-shadow: 0 0 5px rgba(0, 0, 0,0.3);

    &_video{
        width: 100%;
    }
    &_list{
        display: flex;
        width: max-content;
        
        &_name{
            border-bottom: 2px solid grey;
            display: block;
            font-size: 30px;
            font-weight: 600;
            letter-spacing: 2px;
            background: red;
            width: 100px;
            padding: 5px ;
            padding-left: 15px;
        background: rgba(0, 0, 0,0.05);


        }
    }
}

.RemoteTab{
    display: flex;
    padding-top: 10px;
        background: rgba(0, 0, 0,0.3);
        // background-color: ;
    &_tab{
        background: rgba(0, 0, 0,0.0);
        // background-color: red;
        padding: 5px 10px;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        text-transform: uppercase;
        font-weight: 700;
        width: min-content;
        transition-duration: 0.5s;
        

    }
    &_tab_client{
       color: rgb(0, 151, 201) !important; 
    }
    &_tab_remote{
        color: rgb(161, 0, 201) !important; 
     }
 

    &_tab_active{
        background: #ffffff;
        border-radius: 10px 10px 0 0;
        font-size: 18px;
        box-shadow: -0px -5px 10px rgba(0, 0, 0,0.3);

    }
}

.Input{
    // background: yellow;
    
    padding: 5px;
    width: 200px;
    &_label{
        font-weight: 600;
        font-size: 14px;
    }
    &_input{
        letter-spacing: 2px;
        box-shadow: 0 0 5px rgba(0, 0, 0,0.3);

        transition-duration: 0.5s;
        border: none;
        border-bottom: 1px solid grey;
        font-size: 24px;
        font-weight: 700;
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        width: 100%;
        padding: 5px;
        box-sizing: border-box;
        background: rgba(0,0,0,0.05);
        &:focus{
            background: rgba(0,0,0,0.00);
            outline: none;
            border-bottom: 2px solid black;
            
        }
        // width: max-content;
    }

}

.Button{
    border: none;
    font-size: 20px;
    font-weight: 700;
    border: 1px solid grey;
    text-transform: uppercase;
    // height: 100%;
    cursor: pointer;
    padding: 10px;
    box-sizing: border-box;

    &:hover{
    background: rgba(0,0,0,0.0);
    // background: ;
    }
    
    


}